import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

// === renders each tile for Game ====
class Tile extends React.Component {
    // Every class has a render
    render() {

        // Every render has a return
        return (
            <div className="card-image">
                <img src="data:image/jpeg;base64," alt="LOR PIC" />
                {/* TODO */}
            </div> //Makes each tile a button on display
        );
    }
}

// === game board that controls the tiles ====
class Board extends React.Component {

    //determines what information each tile contains
    renderTile(i) {
        return <Tile />; //passes information down to Tile Class
    }

    render() { //Every class has a render
        const gameTitle = "Lord of the Rings Memory Game"; //passed to return in this component
        const instructions = "Click on each image only once. Can you get them all? ";  //passed to retrun in this component

        //Use parentheses to encapsulate all that should be returned
        return (
            <div className = "container">
                <div className="row" id="title-row">
                    <div className="col s12">
                        <h1>{gameTitle} </h1>
                        <h3>{instructions}</h3>
                    </div>
                </div>

                <div className="row" id="game-row">
                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(0)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(1)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(2)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(3)}
                        </div>
                    </div>
                </div>

                <div className="row" id="game-row">
                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(4)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(5)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(6)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(7)}
                        </div>
                    </div>
                </div>

                <div className="row" id="game-row">
                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(8)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(9)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(10)}
                        </div>
                    </div>

                    <div className="col s3">
                        <div className="card">
                                {this.renderTile(11)}
                        </div>
                    </div>
                </div>

            </div>
        ); 
    }
}

class Game extends React.Component {
    render() { //every class has a render
        return ( 
                    <Board />
        ); //every render has a return

    }




}


ReactDOM.render(
    <Game />,
    document.getElementById('root')
);



